.about__container {
    display: grid;
    grid-template-columns: 50% 40%;
    gap: 15%;
}

.flip-box {
    background-color: transparent;
    width: 90%;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    margin: auto;
}

.flip-box-inner {
    position: relative;
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 1rem;
}

.flip-box-inner:hover .flip-box-front {
    transform: rotateX(180deg);
}
  
.flip-box-inner:hover .flip-box-back {
    transform: rotateX(0deg);
}

.flip-box-front {
    border-radius: 1rem;
    overflow: hidden;
    background-color: transparent;
    color: black;
    
}

.flip-box-back {
    border-radius: 1rem;
    overflow: hidden;
    background-color: transparent;
    transform: rotateX(180deg);
}

.flip-box-front, .flip-box-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    transition: transform 0.6s ease;
}

.benny {
    border-radius: 1rem;
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var( --color-bg-variant);
    border:1px solid transparent;
    border-radius: 4rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.btn-contact {
    margin: auto;
}

.icon__h5 {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.6rem;

}

.about__card small {
    font-size: 0.7rem;
    color:var(--color-light);
}

.about__container p {
    margin: 1rem 0 1rem;
    color: var(--color-light);
}

/* ========== MEDIA QUERIES (MEDIUM DEVICES)===========*/
@media screen and (max-width:1024px){
   .about__container {
    grid-template-columns: 1fr;
    width: 70%;
    gap: 1.5rem;
   }
   .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
   }
   .about__content p {
    margin: 1rem 0 1.5rem;
   }

   .flip-box-inner {
    width: 70%;
    margin: auto;
   }
}

/* ========== MEDIA QUERIES (SMALL DEVICES)===========*/
@media screen and (max-width:600px){
    #about {
        margin-top: 0rem;
    }

    .about__container {
    grid-template-columns: 1fr;
    
   }

    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }
    
    .about__content {
        text-align: center;
    }
    .about__content p {
        margin: 1.5rem 0;
    }

    p {
        font-size: 0.8rem;
    }

    h5 {
        font-size: 1.1rem;
    }

    .flip-box {
        background-color: transparent;
        width: 100%;
    }

    .flip-box-inner {
        width: 80%;
       }
}