nav {
    background: #fcf9f0;
    border: 1px solid #00457a;
    width: max-content;
    display: block;
    padding: 0.4rem 0.4rem;
    z-index: 2;
    position: fixed;
    left: 96%;
    transform: translateX(-50%);
    bottom: 30%;
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    border-radius: 1rem 1rem 5rem 5rem;
    backdrop-filter: blur(15px);

}

nav a {
    background: transparent;
    padding: 0.2rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.5rem;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}

nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}


/* ========== MEDIA QUERIES (SMALL DEVICES)===========*/
@media screen and (max-width:700px){
    nav {
        width: 100%;
        flex-direction: row;
        justify-content: space-around;
        left: 50%;
        bottom: 0%;      
        padding: 0.6rem 1rem;       
        position: fixed;        
        gap: 0.8rem;
        border-radius: 0;
        backdrop-filter: blur(15px);
    }

}