@tailwind base;
@tailwind components;
@tailwind utilities;


@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400&display=swap');


*  {
    margin: 0;
    padding: 0;
    border:0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg:#fcf9f0;
    --color-bg-variant:#D2DAFF;
    --color-primary:#00457a;
    --color-primary-variant:#00457a;
    --color-white:#015ea5;
    --color-light:#00457a;

    --transition: all 400ms ease; 

    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-sm:90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Quicksand', sans-serif;
    background: var(--color-bg);
    color: #00457a;
    line-height: 1.7;
    background: -webkit-linear-gradient(to right, #fcf9f0, #8EA7E9);
    background: linear-gradient(to left, #fcf9f0, #8EA7E9);

   
   
    /* background: -webkit-linear-gradient(to right, #fcf9f0, #8EA7E9);  Chrome 10-25, Safari 5.1-6
    background: linear-gradient(to left, #fcf9f0, #8EA7E9); W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
 
}



/*=========== GENERAL STYLES ===========*/


.container {
    width: var(--container-width-lg);
    margin:0 auto;
}

h1,h2,h3,h4,h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem
}

h5 {
    font-size: 1.3rem;
}

section {
    padding-top: 6rem;
    padding-bottom: 6rem;
    /* background-image: url(../src/assets/cloud.png); 
    background-size: cover;
    background-repeat: round;
    background-position: center; */
}

section > h2,
section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: #035491;
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background:var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

.separation {
    margin: auto;
    content: '';
    display: block;
    height: 1px;
    width: 40%;
    background: #9FC9F3;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #EEEEEE, #9FC9F3);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #EEEEEE, #9FC9F3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  }
  
  
/*=========== MEDIA QUERIES (MEDIUM DEVICES)===========*/

@media screen and (max-width:1024px){
    .container {
        width: var(--container-width-md);
    }
    
}


/*=========== MEDIA QUERIES (SMALL DEVICES)===========*/

@media screen and (max-width:600px){
    .container {
        width: var(--container-width-sm);
}

    section {
        padding-top: 3rem;
        padding-bottom: 3rem;
    }

    section > h2{
        margin-bottom: 2rem;
    }

    h1 {
        font-size: 2rem
    }
}